<template>
  <div id="app">
    <!-- 只有登录后才显示侧边栏和顶部导航 -->
    <div v-if="isLoggedIn" class="app-container">
      <!-- 侧边导航栏 -->
      <aside class="sidebar">
        <div class="logo">Admin System</div>
        <nav>
          <ul>
            <li>
              <router-link to="/dashboard" class="nav-link">仪表盘</router-link>
            </li>
            <li>
              <router-link to="/users" class="nav-link">用户管理</router-link>
            </li>
            <li>
              <router-link to="/settings" class="nav-link">系统设置</router-link>
            </li>
          </ul>
        </nav>
      </aside>

      <!-- 主内容区 -->
      <main class="main-content">
        <!-- 顶部导航 -->
        <header class="header">
          <h1>{{ $route.meta.title }}</h1>
          <button @click="logout" class="logout-btn">退出登录</button>
        </header>

        <!-- 页面内容 -->
        <div class="content">
          <router-view />
        </div>
      </main>
    </div>

    <!-- 未登录时显示登录页 -->
    <router-view v-else />
  </div>
</template>

<script setup>
import { useRouter } from 'vue-router'
import { ref, onMounted } from 'vue'

// 状态管理：登录状态
const isLoggedIn = ref(false)
const router = useRouter()

// 检查本地存储中的登录状态
onMounted(() => {
  const loggedIn = localStorage.getItem('isLoggedIn')
  isLoggedIn.value = loggedIn === 'true'

  // 如果未登录且不在登录页，跳转到登录页
  if (!isLoggedIn.value && router.currentRoute.value.path !== '/login') {
    router.push('/login')
  }
})

// 退出登录
const logout = () => {
  localStorage.setItem('isLoggedIn', 'false')
  isLoggedIn.value = false
  router.push('/login')
}
</script>

