

<template>
  <div class="users-page">
    <div class="page-header">
      <h2>用户管理</h2>
      <button @click="showAddUserModal = true" class="add-btn">添加用户</button>
    </div>

    <!-- 搜索框 -->
    <div class="search-box">
      <input
        type="text"
        v-model="searchQuery"
        placeholder="搜索用户名或邮箱..."
        @input="filterUsers"
      >
    </div>

    <!-- 用户列表 -->
    <table class="users-table">
      <thead>
        <tr>
          <th>ID</th>
          <th>用户名</th>
          <th>邮箱</th>
          <th>角色</th>
          <th>状态</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in filteredUsers" :key="user.id">
          <td>{{ user.id }}</td>
          <td>{{ user.name }}</td>
          <td>{{ user.email }}</td>
          <td>{{ user.role }}</td>
          <td>
            <span class="status-badge" :class="user.status === 'active' ? 'active' : 'inactive'">
              {{ user.status === 'active' ? '活跃' : '禁用' }}
            </span>
          </td>
          <td class="actions">
            <button @click="editUser(user)" class="edit-btn">编辑</button>
            <button @click="deleteUser(user.id)" class="delete-btn">删除</button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- 添加/编辑用户弹窗 -->
    <div v-if="showAddUserModal || showEditModal" class="modal-backdrop">
      <div class="modal">
        <div class="modal-header">
          <h3>{{ showEditModal ? '编辑用户' : '添加用户' }}</h3>
          <button @click="closeModal" class="close-btn">&times;</button>
        </div>

        <div class="modal-body">
          <div class="form-group">
            <label>用户名</label>
            <input type="text" v-model="currentUser.name">
          </div>

          <div class="form-group">
            <label>邮箱</label>
            <input type="email" v-model="currentUser.email">
          </div>

          <div class="form-group">
            <label>角色</label>
            <select v-model="currentUser.role">
              <option value="admin">管理员</option>
              <option value="editor">编辑</option>
              <option value="viewer">查看者</option>
            </select>
          </div>

          <div class="form-group">
            <label>状态</label>
            <select v-model="currentUser.status">
              <option value="active">活跃</option>
              <option value="inactive">禁用</option>
            </select>
          </div>
        </div>

        <div class="modal-footer">
          <button @click="closeModal" class="cancel-btn">取消</button>
          <button @click="saveUser" class="save-btn">保存</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'

// 用户数据
const users = ref([
  { id: 1, name: 'admin', email: 'admin@example.com', role: 'admin', status: 'active' },
  { id: 2, name: 'editor1', email: 'editor1@example.com', role: 'editor', status: 'active' },
  { id: 3, name: 'viewer1', email: 'viewer1@example.com', role: 'viewer', status: 'active' },
  { id: 4, name: 'user4', email: 'user4@example.com', role: 'viewer', status: 'inactive' }
])

// 搜索和筛选
const searchQuery = ref('')
const filteredUsers = computed(() => {
  if (!searchQuery.value) return users.value

  const query = searchQuery.value.toLowerCase()
  return users.value.filter(user =>
    user.name.toLowerCase().includes(query) ||
    user.email.toLowerCase().includes(query)
  )
})

// 弹窗状态
const showAddUserModal = ref(false)
const showEditModal = ref(false)
const currentUser = ref({ id: null, name: '', email: '', role: 'viewer', status: 'active' })

// 打开编辑弹窗
const editUser = (user) => {
  currentUser.value = { ...user } // 复制用户数据
  showEditModal.value = true
}

// 关闭弹窗
const closeModal = () => {
  showAddUserModal.value = false
  showEditModal.value = false
  currentUser.value = { id: null, name: '', email: '', role: 'viewer', status: 'active' }
}

// 保存用户（添加或编辑）
const saveUser = () => {
  if (showEditModal.value) {
    // 编辑现有用户
    const index = users.value.findIndex(u => u.id === currentUser.value.id)
    if (index !== -1) {
      users.value[index] = { ...currentUser.value }
    }
  } else {
    // 添加新用户
    const newId = Math.max(...users.value.map(u => u.id), 0) + 1
    users.value.push({
      ...currentUser.value,
      id: newId
    })
  }

  closeModal()
}

// 删除用户
const deleteUser = (id) => {
  if (confirm('确定要删除这个用户吗？')) {
    users.value = users.value.filter(user => user.id !== id)
  }
}

// 筛选用户
const filterUsers = () => {
  // 由computed属性自动处理
}
</script>

