

<template>
  <div class="settings-page">
    <h2>系统设置</h2>

    <div class="settings-card">
      <h3>基本设置</h3>

      <div class="form-group">
        <label for="siteName">网站名称</label>
        <input
          type="text"
          id="siteName"
          v-model="settings.siteName"
        >
      </div>

      <div class="form-group">
        <label for="siteDescription">网站描述</label>
        <textarea
          id="siteDescription"
          v-model="settings.siteDescription"
          rows="3"
        ></textarea>
      </div>

      <div class="form-group">
        <label>
          <input
            type="checkbox"
            v-model="settings.enableRegistration"
          >
          允许用户注册
        </label>
      </div>

      <div class="form-group">
        <label>默认语言</label>
        <select v-model="settings.defaultLanguage">
          <option value="zh-CN">简体中文</option>
          <option value="en">英文</option>
          <option value="ja">日语</option>
        </select>
      </div>

      <button @click="saveSettings" class="save-btn">保存设置</button>
      <p class="success-message" v-if="showSuccessMessage">设置已保存成功！</p>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'

// 系统设置数据
const settings = ref({
  siteName: '后台管理系统',
  siteDescription: '这是一个简单的后台管理系统',
  enableRegistration: true,
  defaultLanguage: 'zh-CN'
})

// 保存成功提示
const showSuccessMessage = ref(false)

// 页面加载时读取本地存储的设置
onMounted(() => {
  const savedSettings = localStorage.getItem('systemSettings')
  if (savedSettings) {
    settings.value = JSON.parse(savedSettings)
  }
})

// 保存设置
const saveSettings = () => {
  // 保存到本地存储（实际项目中会保存到服务器）
  localStorage.setItem('systemSettings', JSON.stringify(settings.value))

  // 显示成功提示
  showSuccessMessage.value = true
  setTimeout(() => {
    showSuccessMessage.value = false
  }, 3000)
}
</script>

